
body{
  background-color: #F5F5F5;
  color: black;
  overflow-x: hidden;
}

html{
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth
}

section {
  height: 100vh;

}

.my--header{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Rubik', sans-serif;

}
.my--header h1{
  font-size: 70px;
}
.main--header{
  display: flex;
  justify-content: center;
  align-items: center;


}

.my--ptag{
  position: absolute;
  top: 500px;
  left: 100px;
  font-size: 80px;
  opacity: 0.2;
  font-family: 'Rubik', sans-serif;
  font-weight: 100;

  
}

.test--header{
  
  position: relative;
  font-family: 'Rubik', sans-serif;


}

.first-line{
  position: relative;

}

.first-bar{
  position: absolute;
  top: 20px;
  bottom: 10px;
  background-color: black;
  left: 0;
  right: 0;
  animation: bar 2.5s ease-in-out forwards;
}

.second-line{
  position: relative;
}

.second-bar{
  position: absolute;
  top: 20px;
  bottom: 10px;
  background-color: black;
  left: 0;
  right: 0;
  animation: bar 2.7s ease-in-out forwards;
}

.third-line{
  position: relative;
}

.third-bar{
  position: absolute;
  bottom: 10px;
  top: 20px;
  left: 0;
  right: 0;
  background-color: black;
  animation: bar 2.5s ease-in-out forwards;
}



.about-sub{
  top: -15px;
  bottom: 380px;
  position: absolute;
  width: 120px;
  background-color: black;
  font-family: 'Rubik', sans-serif;

  left: 120px;
  animation: swipe-in-text 1.5s  ease-in-out forwards;


  
  
}


.about--con{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  margin-left: 200px;
  font-family: 'Rubik', sans-serif;
  margin-top: 200px;
line-height: 4rem;
font-size: 20px;



}





.aboutme--con{
