.list--skills li{
 list-style: none;
 font-family: 'Montserrat', sans-serif;
 font-size: 20px;
 padding: 10px;
 font-weight: bold;
 color: white;
 background-color: black;
 


}





.language--con h1{
  font-family: 'Montserrat', sans-serif;
  font-weight: lighter;
