}

form{
  display: flex;
  
  flex-direction: column;
}


form input, textarea, button{
  margin: 20px;
  padding:  25px 60px;
  background-color: white;
  border: 2px solid black;
  border-radius: 10px;
}



button{
  padding: 20px 40px;
}

.error-div{
  font-family: 'Montserrat', sans-serif;

}



button:active{
  background-color: black;
  color: white;
}

@keyframes moveBackAndForth {
  0% {
    transform: translateX(0);
  }
  
  0% {
    transform: translateX(20%);
  }


}

@keyframes swipe-in-text {
  0% {
    
    left:120;
    width: 230px;
  }
  
  50% {
    left: 120;
    width: 230px;;
    
  }

  100%{
    left: 120;
    width: 0%
  }
}

@keyframes bar {
  0%
  {
    width: 100%;
    left: 0
  }

  50%
  {
    width: 100%;
    left: 0
  }
  100%
  {
    width: 0%;
    left: 0
  }
}